*{
  margin: 0;
  padding: 0;
}
body{
  background: #09a4d3;
  width: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 9vmin;
}

.App-header {
  background-color: #282c34;
  margin: auto;
  color: #09d3ac;
  height: auto;
  width: auto;
  padding-bottom: 10px;
}

.App-link {
  color: #09d3ac;
}

/*------------------------------------------------------------*/

form{
  border: 0px solid black;
  padding: 10px;
}
input{
  width: 500px;
  height: 45px;
  margin: 0;
  border-radius: 5px;
  background: rgba(255, 255, 254, 0.205);
  border: 0;
  border-bottom: 4px solid rgb(70, 70, 70);
}

textarea{
  width: 500px;
  height: 155px;
  border-radius: 5px;
  border: 0;
  border-bottom: 4px solid rgb(70, 70, 70);
  background: rgba(255, 255, 254, 0.205);
}

textarea:focus,input:focus{
  outline: none;
}

.label{
  margin: auto;
  width: 200px;
  height: 40px;
  text-align: center;
  margin-bottom: -10px;
}

button{
  width: 100px;
  height: 35px;
  background: rgb(252, 169, 158);
  border: 0px;
  border-radius: 3px;
  color: rgb(34, 34, 34);
  font-size: 1rem;
}:hover{
  color: white;
}

p{
  font-family: 'Open Sans';
  line-height: 1.8rem;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
  padding: 8px 0 10px 15px;
  width: 80%;
  margin: auto
}
li{
  font-family: 'Times New Roman', Times, serif;
  padding: 5px 0 5px 5px;
}
li h3{
  font-size: 1.5rem;
  width: 80%;
  margin: auto;
}

/*---------------------------*/

@media screen and (min-width: 768px){
  input{
    width: 300px;
    height: 30px;
  }
  textarea{
    width: 300px;
    height: 80px;
  }
}

@media screen and (max-width: 591px){
  input{
    width: 250px;
    height: 35px;
  }
  textarea{
    width: 250px;
    height: 80px;
  }
}